.menu-container {
  // position: relative;
  // width: 100vw;
  // padding-top: 1%;
  margin-right: 5%;
  display: flex;
  flex-direction: row-reverse;
  // position: fixed;
  font-size: 0.9rem;
}

.menu {
  background: #FDF7F0;
  border-radius: 8px;
  position: absolute;
  top: 60px;
  right: 0;
  width: 225px;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.3);
  opacity: 0;
  visibility: hidden;
  transform: translateY(-20px);
  transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s;
  z-index: 99;
  margin-right: 5%;
}

.menu.active {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  // margin-right: 6%;
}

.menu ul {
  list-style: none;
  padding: 0;
  // padding-right: 2%;
  margin: 0;
}



.menu li {
  // border-bsottom: 1px solid #dddddd;
}

.menu li a {
  text-decoration: none;
  color: #333333;
  padding: 15px 20px;
  display: block;
  cursor: pointer;
  color: #051885;

}

.dropdowns:last-child a {
  border-top: 2px solid #FFA7E5 !important;
  cursor: pointer;
}


.menu-trigger {
  display: block;
  width: 40px;
  background-color: transparent;
  cursor: pointer;


  span {
    margin: 0%;
    font-size: 0px;

  }

  .burger-icon {
    display: flex;
    flex-direction: column;

    .burger-icon-line {
      margin-top: 2.5px;
      margin-bottom: 2.5px;
      border: 1.5px solid #FFA7E5;
      border-radius: 3px;
      width: 80%;
      margin-left: 10%;
      visibility: visible;
    }
  }
}



.menu-trigger span {
  font-weight: 700;
  vertical-align: middle;
  font-size: 14px;
  margin: 0 10px;
}



